* {

  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  margin: 0;
  padding: 0;
 
}
.country-name-logo{
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.country-item {
  list-style: none;
  display: flex;
  align-items: center;
}

.contry-name {
  margin-left: 15px;
  margin-top: 5px;
  margin-bottom: 0;
}

